/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

img {
  max-width: 100%;
  height: auto; }

*, *::before, *::after {
  box-sizing: border-box; }

.hide-text {
  text-indent: 101%;
  overflow: hidden;
  white-space: nowrap; }

.material-icons {
  display: inline;
  vertical-align: middle;
  line-height: 1px; }

nav {
  width: 296px;
  height: 100vh;
  position: fixed; }

.menu .logo a {
  line-height: 2.5;
  display: block;
  background: url(https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week5/logo-ANSWER.svg) no-repeat center/cover; }

.menu ul a {
  color: #212529; }
  .menu ul a:hover {
    color: #146c43; }

.main {
  margin-left: 296px; }
  .main .notify a {
    color: #000; }
    .main .notify a:hover {
      color: #146c43; }

.user img {
  width: 40px;
  height: 40px;
  border-radius: 50%; }

.table thead {
  border-bottom: 1px solid black; }

.table .material-icons {
  color: #000; }
  .table .material-icons:hover {
    color: #146c43; }

.page-link:hover {
  background-color: #146c43;
  color: #FFF; }

a {
  display: block;
  text-decoration: none; }

ul, ol {
  list-style: none; }

img {
  max-width: 100%;
  height: auto; }
